import React, { Component }  from 'react';
class School extends Component{

    constructor(props) {
          super(props);
          this.handleClickEdit = this.handleClickEdit.bind(this);

    this.state = {
            isLoading: true,
            selected: false,
            update: false,
            addSchool: false,
            schools : [],
            selectedSchool: []
        };
  }
    emptlySchool = {	    		
      schoolId: "",
      schoolPhone: "",
      schoolAddress: "",
      schoolName: ""
  }
    //FETCH SCHOOLS
    async componentDidMount() {
      const response = await fetch('http://localhost:8080/api/school', {mode:"no-cors",  
        method: 'GET',
        headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    }}, );
      const body = await response.json();
      console.log("BODY : " + body);
      this.setState({ schools: body, isLoading: false, update: false });
    }
    
    //EDIT SCHOOL
    async handleClickEdit(schoolId) {
      console.log(schoolId);

      await fetch('http://localhost:8080/api/school/{schoolId}', {mode: 'no-cors', 
        method: 'PUT',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
      }).then(async(response) => {
            this.setState({update: true,addSchool: false, selectedSchool: await response.json()});
        });
      };

      //REMOVE SCHOOL
      async remove(schoolId) {
        console.log(schoolId + ` is deleted. ` );
             await fetch(`http://localhost:8080/api/school/${schoolId}`, {
               mode:"no-cors",
               method: 'DELETE',
               headers: {
                 'Accept': 'application/json',
                 'Content-Type': 'application/json'
               }
             }).then(() => {
               window.location.reload();
             });
       }	

    render(){
      const schools = this.state;
        return (
       <table>
              <thead>
                <tr>
                  <th scope="col">School Name</th>
                  <th scope="col">School Address</th>
                  <th scope="col">School Phone</th>
                  <th scope="col" colspan="3">Settings</th>
                </tr>
              </thead>
             <tbody>
              {schools.map(school =>
                <tr id={schools.schoolId}>
                    <td>{school.schoolName} </td>
                    <td>{school.schoolAddress}</td>
                    <td>{school.schoolPhone}</td>
                    
                    <td>
                      <button onClick={() => {this.handleClickAdd(school.schoolId)}}> Add </button>
                  </td>

                    <td>
                        <button onClick={() => {this.handleClickEdit(school.schoolId)}}> Edit </button>
                    </td>
                    <td>
                        <button onClick={() => {this.remove(school.schoolId)}}> Delete </button>
                    </td>

                </tr>
              )}
            </tbody>
      </table>
          
      );
  }
}







export default School;