import React from "react";
import { Link } from "react-router-dom";
import './Header.css';

function Header() {
  return (
    <div >
      <ul>
        <h2>
          <li>
            <Link to="/">HOME</Link>
            </li>
          <li>
             <Link to="/school">SCHOOL</Link>
             </li>
          <li>
            <Link to="/instructor">INSTRUCTOR</Link>
          </li>
          <li>
            <Link to="/student">STUDENT</Link>
            </li>
        </h2>
      </ul>
    </div>
  );
}



export default Header;